import React, { memo } from 'react';
import ArticleListItem from '~/components/ArticleList/Item';

import { Container } from './styles';

interface Props {
    posts: GatsbyTypes.BlogIndexQuery['allMarkdownRemark']['nodes'],
}

const ArticleList = ({posts}: Props) => {
    return (
        <Container>
            <ul>
                {posts
                    .filter(post => post && post.frontmatter?.title) // íí°ë§: postê° undefinedê° ìëê³ , titleì´ ìë ê²½ì°ìë§ ì§í
                    .map(post => {
                        const title = post.frontmatter?.title ?? post.fields?.slug ?? '';
                        const slug = post.fields?.slug ?? '';
                        const description = post.frontmatter?.description ?? post.excerpt ?? '';

                        if (!title || !slug) {
                            return null; // titleì´ë slugê° ìë ê²½ì° ë ëë§íì§ ìì
                        }

                        return (
                            <ArticleListItem
                                key={slug}
                                title={title}
                                slug={slug}
                                description={description}
                            />
                        );
                    })}
            </ul>
        </Container>
    );
};

export default memo(ArticleList);
